.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__pic {
  background-image: url("imac.202c52ff.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 160px 134px;
  align-self: center;
  width: 160px;
  height: 134px;
}

.card__name {
  text-align: left;
  color: #060b35;
  width: 166px;
  height: 36px;
  margin-top: 40px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__code {
  text-align: left;
  color: #616070;
  width: auto;
  height: auto;
  margin-top: 4px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  text-align: right;
  justify-content: space-between;
  align-self: center;
  align-items: center;
  width: 166px;
  height: 16px;
  margin-top: 16px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price {
  text-align: left;
  color: #616070;
  justify-content: space-between;
  margin-top: 24px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.card__price-amount {
  text-align: right;
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  cursor: pointer;
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card:hover .card__name {
  color: #34568b;
}

.stars {
  justify-content: space-between;
  width: 96px;
  height: 16px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars_star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--1 :first-child, .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.header__link {
  width: 40px;
  height: 40px;
}

.nav {
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 60px;
  padding: 0;
  display: flex;
}

.nav__list {
  text-transform: uppercase;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 60px;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  align-content: center;
  height: 60px;
  margin-right: 20px;
  font-size: 12px;
  list-style-type: none;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  text-wrap: nowrap;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: " ";
  background-color: #00acdc;
  border-radius: 4px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  transform: translateY(-100%);
}

body {
  margin: 0;
}

.container {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .container {
    grid-template-columns: repeat(4, 200px);
  }
}
